import { ApexOptions } from "apexcharts";
import axios from "axios";
import React, { Component } from "react";
import ReactApexChart from "react-apexcharts";
import { GetGeneralSummaryInterface } from "../../containers/Claimings/Admin/ClaimReport";
import { education_category, PlansTypeKeys } from "../../interfaces";
import { API } from "../../utils/api";
import Loading from "../Loading/Loading";

interface GeneralSummaryPie {
  series: number[];
  options: ApexOptions;
}

interface ClaimReportAnalyticProps {
  general_summary: {
    pending: number;
    validated: number;
    total: number;
  };
  summary_details: {
    position_title: string;
    total: number;
    pending: number;
    validated: number;
  }[];
  claim_level: number;
  institution: education_category;
  general_main_data: GetGeneralSummaryInterface;
  selected_claim_type: PlansTypeKeys;
}

interface ClaimReportAnalyticState {
  loading: boolean;
}

export class ClaimReportAnalytic extends Component<
  ClaimReportAnalyticProps,
  ClaimReportAnalyticState
> {
  constructor(props: ClaimReportAnalyticProps) {
    super(props);

    this.state = {
      loading: true,
    };
  }
  setLoading = (status: boolean) => this.setState({ loading: status });
  render() {
    return (
      <div className="animate__animated animate__fadeIn">
        <div className="flex flex-row items-center gap-4 h-64">
          <div className="bg-gray-100 rounded p-4 border border-blue-600 h-full">
            <div>
              <div className="font-extrabold text-xl">General summary</div>
            </div>
            <div id="chart">
              <ReactApexChart
                options={{
                  chart: {
                    width: 380,
                    type: "donut",
                  },
                  labels: [
                    `Total Completed claims (${this.props.general_main_data.completedClaimCounts})`,
                    `Total evaluated claims (${this.props.general_main_data.evaluatedClaimCounts})`,
                    `Total pending claims (${this.props.general_main_data.pendingClaimCounts})`,
                    `Total Canceled claims (${this.props.general_main_data.deletedClaimCounts})`,
                  ],
                  responsive: [
                    {
                      breakpoint: 480,
                      options: {
                        chart: {
                          width: 200,
                        },
                        legend: {
                          position: "bottom",
                        },
                      },
                    },
                  ],
                }}
                series={[
                  this.props.general_main_data.completedClaimCounts,
                  this.props.general_main_data.evaluatedClaimCounts,
                  this.props.general_main_data.pendingClaimCounts,
                  this.props.general_main_data.deletedClaimCounts,
                ]}
                type="donut"
                width={400}
              />
            </div>
          </div>
          <div className="bg-gray-100 rounded p-4 border border-blue-600 h-full">
            <div>
              <div className="font-extrabold text-xl">
                {this.props.selected_claim_type}
              </div>
              <div className="font-extrabold text-base text-blue-600">
                Claim level:{" "}
                {this.props.claim_level === 1
                  ? "Level one"
                  : "Public Service Commission"}
              </div>
            </div>
            <div id="chart">
              <ReactApexChart
                options={{
                  chart: {
                    width: 380,
                    type: "pie",
                  },
                  labels: [
                    `Total pending claims (${this.props.general_summary.pending})`,
                    `Total evaluated claims (${this.props.general_summary.validated})`,
                  ],
                  responsive: [
                    {
                      breakpoint: 480,
                      options: {
                        chart: {
                          width: 200,
                        },
                        legend: {
                          position: "bottom",
                        },
                      },
                    },
                  ],
                }}
                series={[
                  this.props.general_summary.pending,
                  this.props.general_summary.validated,
                ]}
                type="pie"
                width={400}
              />
            </div>
          </div>
        </div>
        <div className="animate__animated animate__fadeIn rounded border border-blue-600 pt-3 pr-2 mt-4">
          <div id="chart" className="h-auto">
            <ReactApexChart
              options={{
                chart: {
                  type: "bar",
                  height: "auto",
                },
                plotOptions: {
                  bar: {
                    borderRadius: 2,
                    horizontal: true,
                    barHeight: "100px",
                  },
                },
                dataLabels: {
                  enabled: true,
                },
                xaxis: {
                  categories: this.props.summary_details.map(
                    (item) => item.position_title
                  ),
                },
              }}
              series={[
                {
                  name: "Total claims",
                  data: this.props.summary_details.map((item) => item.total),
                },
                {
                  name: "Pending claims",
                  data: this.props.summary_details.map((item) => item.pending),
                },
                {
                  name: "Validated claims",
                  data: this.props.summary_details.map(
                    (item) => item.validated
                  ),
                },
              ]}
              type="bar"
              height={this.props.summary_details.length > 60 ? 5000 : 3000}
            />
          </div>
          {console.log("Length: ", this.props.summary_details.length)}
        </div>
      </div>
    );
  }
}

export default ClaimReportAnalytic;
